$cor-0: #ffffff;
$cor-1: #fcfcfc;
$cor-2: #ededed;
$cor-3: #cccccc;
$cor-4: #b2b2b2;
$cor-5: #9c9c9c;
$cor-6: #717171;
$cor-7: #595959;
$cor-8: #404040;
$cor-9: #2e2e2e;
$cor-10: #2e2e2e;
$cor-11: #1a1a1a;
$cor-12: #0e0e0e;
$cor-13: #000000;
$cor-p1: #14DBEB;
$cor-p2: #0C00FF;
$redes-0: #1C92FF;
$redes-1: #181717;
$redes-2: #16A301;


$f-device: 1200px;
$t-device: 760px;
$m-device: 480px;

// Tipografia
@mixin tipo-1($tamanho) {
  font-family: 'Nunito', sans-serif;
  @if $tamanho == 14 {
    font-size: 14px;
    line-height: 1;
    font-weight: 500;
  } @else if $tamanho == 18 {
    font-size: 18px;
    line-height: 1.3;
  } @else if $tamanho == 24 {
    font-size: 24px;
    line-height: 1.3;
  }
}
@mixin tipo-2($tamanho) {
  font-family: 'Alice', sans-serif;
  @if $tamanho == 72 {
    font-size: 72px;
    font-weight: 400;
  }
}

@mixin d($dispositivo) {
  @if $dispositivo == f {
    @media (max-width: $f-device) {
      @content;
    }
	} @else if $dispositivo == t {
    @media (max-width: $t-device) {
      @content;
    }
  } @else if $dispositivo == m {
    @media (max-width: $m-device) {
      @content;
    }
  }
}

